.contact{
    background-color: lightslategray;


}
.contact_container{
    display: flex;
    margin: 0 auto;
    width: 80%;
    border: 1px solid;
    justify-content: space-around;
    flex-wrap: wrap;

}
.communication{
    color: darkred;
    border: 1px solid;
}

.communication_table{
    padding: 5px;
    margin:  0 auto;
    width: 270px;

}

.thoughts{
    max-width: 800px;
    font-style: italic;
    border: 1px solid dodgerblue;
    padding: 5px;
}
form{
    margin:  0 auto;
}
input{
    padding: 10px;
    margin: 5px;
    width: 240px;
}
textarea{
    padding: 10px;
    margin: 5px;
    width: 240px;
    height: 50px;
    font-size: 18px;
}

@media screen and (min-width:767px ){
    .communication_table{
        padding: 5px;
        margin:  0 auto;
        width: 370px;

    }
    form{
        margin:  0 auto;
    }
    input{
        padding: 10px;
        margin: 5px;
        width: 340px;
    }
    textarea{
        padding: 10px;
        margin: 5px;
        width: 340px;
        height: 50px;
        font-size: 18px;
    }
}